.crawler div {
  background-color: #0061d5;
  display: inline-block;
  -webkit-animation: crawler .66s infinite ease-in-out;
  animation: crawler .66s infinite ease-in-out;
  width: 2px;
  height: 10px;
  border-radius: 4px; }

.btn-primary .crawler div {
  background-color: #fff; }

.crawler.is-small {
  -webkit-transform: scale(0.7);
          transform: scale(0.7); }

.crawler.is-medium {
  -webkit-transform: scale(1.5);
          transform: scale(1.5); }

.crawler.is-large {
  -webkit-transform: scale(2);
          transform: scale(2); }

.is-loading .crawler div {
  will-change: transform, opacity;
  -webkit-transform: translateZ(0);
          transform: translateZ(0); }

.crawler div:nth-child(2) {
  margin-left: 2px;
  -webkit-animation-delay: .1s;
  animation-delay: .1s; }

.crawler div:last-child {
  margin-left: 2px;
  left: 8px;
  -webkit-animation-delay: .2s;
  animation-delay: .2s; }

@-webkit-keyframes crawler {
  0%,
